/* eslint-disable react/forbid-prop-types */
/* eslint-disable react/no-array-index-key */
/* eslint-disable react/no-unescaped-entities */
import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import tw from "tailwind.macro"
import { StaticQuery, graphql } from "gatsby"
import LinkTo from "../components/LinkTo"
import Layout from "../components/layout"
import SEO from "../components/seo"
import AppWrapper from "../views/AppWrapper"
import Content, { Inner } from "../elements/Content"
import Hero from "../views/Hero"
import { BigTitleLight, H2, Strong, P, PLight } from "../elements/Text"
import Button from "../elements/Button"

const SectionWrapper = styled(Inner)``

const SectionContainer = styled.div`
  ${tw``}
`

const PostTitle = styled.span`
  ${tw`block mt-4`};
`

const SectionHeader = styled(H2)`
  ${tw`mb-0`};
`

const Separator = styled.span`
  ${tw`w-full bg-grey-light h-px mx-auto my-6 block`};
`

const SectionContents = styled.div`
  ${tw`flex flex-col`};
`

const Title = styled(P)`
  ${tw`mt-0 mb-0 text-grey-dark`};
`

const Section = ({ children, ...props }) => {
  const { title, author, slug, readingTime } = props

  return (
    <SectionContainer>
      <SectionHeader>
        <LinkTo href={slug}>
          <PostTitle>{title}</PostTitle>
        </LinkTo>
      </SectionHeader>
      <Title>
        By <Strong>{author}</Strong> â€¢ {readingTime}
      </Title>
      <SectionContents>{children}</SectionContents>
      <Separator />
    </SectionContainer>
  )
}

Section.propTypes = {
  children: PropTypes.node.isRequired,
  title: PropTypes.string.isRequired,
  author: PropTypes.string.isRequired,
  slug: PropTypes.string.isRequired,
  readingTime: PropTypes.string.isRequired,
  date: PropTypes.string.isRequired,
}

const BlogPage = () => (
  <StaticQuery
    query={graphql`
      query ListBlogQuery {
        allMarkdownRemark(
          filter: {
            fileAbsolutePath: { regex: "/(blog)/" }
            frontmatter: { hidden: { ne: true } }
          }
          sort: { order: DESC, fields: [frontmatter___date] }
        ) {
          edges {
            node {
              fields {
                slug
                readingTime {
                  text
                }
              }
              frontmatter {
                date(formatString: "MMMM Do YYYY")
                title
                author
                short_description
                hidden
              }
            }
          }
        }
      }
    `}
    render={data => {
      return (
        <Layout>
          <SEO title="The Featurist Blog" />
          <AppWrapper>
            <Hero isDark isBlog style={{ zIndex: 20 }}>
              <BigTitleLight>Blog</BigTitleLight>
              <PLight />
            </Hero>
            <Content>
              <SectionWrapper condensed>
                {data.allMarkdownRemark.edges.map(({ node }, i) => {
                  return (
                    <Section
                      key={`blog-post-${i}`}
                      title={node.frontmatter.title}
                      author={node.frontmatter.author}
                      date={node.frontmatter.date}
                      slug={node.fields.slug}
                      readingTime={node.fields.readingTime.text}
                    >
                      <P>{node.frontmatter.short_description}</P>
                      <LinkTo href={node.fields.slug}>
                        <Button>Read More</Button>
                      </LinkTo>
                    </Section>
                  )
                })}
              </SectionWrapper>
            </Content>
          </AppWrapper>
        </Layout>
      )
    }}
  />
)

export default BlogPage
